@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ledger-button {
    border: none;
    cursor: pointer;
    transition: 0.2s;
    padding: 0px;
    flex-shrink: 0;
}

.ledger-button:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    transition: 0.2s;
}

.update-button {
    width: 100px;
    height: 30px;
    border-radius: 20px;
    display: block;
    margin: auto;
}

.transaction-entry {
    background-color: rgb(238, 238, 238);
    padding-left: 10px;
    margin: 10px;
    border-left: 5px solid rgb(71, 151, 255);

    display: flex;
    flex-direction: row;
    justify-content: space-between;

    border-radius: 3px;
}

.transaction-entry:nth-child(2n){
    background-color: rgb(213, 213, 213);
}

.transaction-entry:hover {
    background-color: rgb(247, 247, 247);
    transition: 0.2s;
}

.UpperBox {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: left;
}

.EntryName {
    margin-top: 5px;
    color: rgb(165, 165, 165);
}

.EntryName > span{
    font-size: 1.3em;
    font-weight: 500;
    color: black;
}

.OwnedBy {
    font-size: 0.8em;
    font-style: italic;
    color: rgb(165, 165, 165);
}

.OwnedBy > span {
    font-weight: 400;
    font-style: unset;
    color: black;
}

.AmountAndDelete {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.AmountAndDelete button{
    height: 100%;
    background-color: rgba(255, 255, 255, 0);
    border: none;
    border-left: 1px solid rgba(0, 0, 0, 0.234);
    width: 30px;
    cursor: pointer;
}

.AmountAndDelete button:hover{
    background-color: rgba(255, 255, 255, 0.344);
}

.Amount {
    display: inline-block;
    width: 75px;
    
    color: rgb(95, 95, 95);
}

.Amount > span {
    color: black;    
    font-size: 1.4em;
    margin-left: 3px;
}

.IndividualTransaction {
    margin: 10px 0px;
}

.IndividualTransaction>.PersonName {
    font-size: 1.5em;
}

.PersonUsed {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-right: 20px;
    grid-gap: 4px;
    gap: 4px
}

.name-tag {
    background-color: rgb(118, 162, 255);
    border-radius: 5px;
    width: 75px;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
}

.AddTag {
    background-color: rgb(182, 182, 182);
    border-radius: 3px;
    width: 75px;
    border: none;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    transition: 0.1s;
    cursor: pointer;
}

.AddTag:hover{
    background-color: rgb(208, 208, 208);
    transition: 0.1s;
}

.name-tag-name {
    margin: 2px auto;
    font-size: 0.8em;
}

.AddTagName {
    margin: auto;
    color: rgb(145, 145, 145);
}

.name-tag-remove-button {
    border-style: none;
    border-left: 1px solid rgba(0, 0, 0, 0.227);
    background-color: rgba(109, 63, 63, 0);
    padding-left: 4px;
    padding-right: 4px;
    cursor: pointer;
    height: 100%;
}

.name-tag-remove-button:hover{
    background-color: rgba(255, 255, 255, 0.364);
    color: white;
    transition: 0.1s;
}


.Record {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.Record[transactionsign = 'negative']{
    background-color: rgba(255, 0, 0, 0.364);
}

.Record[transactionsign = 'negative']:nth-child(2n){
    background-color: rgba(255, 0, 0, 0.207);
}

.Record[transactionsign = 'positive']{
    background-color: rgba(0, 255, 98, 0.364);
}

.Record[transactionsign = 'positive']:nth-child(2n){
    background-color: rgba(0, 255, 98, 0.22);
}


.RecordNameAndShare {
    display: flex;
    flex-direction: row;
    width: 70vw;
    justify-content: space-between;
    align-items: center;

    margin-top: 2px;
    margin-bottom: 2px;

}

.SharedWith {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    width: 220px;

    font-size: 0.7em;
    font-style: italic;
    color: rgb(94, 94, 94);
}

.SharedWith > span {
    font-style: normal;
    font-weight: 800;
}

.RecordTransactionName {
    padding-left: 10px;
    /* padding-top: 2px;
    padding-bottom: 2px; */
}

.RecordAmountAndCross {
    width: 60px;
    font-weight: 500;
    display: flex;
    flex-direction: row;
}

.Total {
    display: flex;
    flex-direction: row;
    justify-content:right;
    align-items:baseline;

    padding-left: 10px;
    padding-right: 20px;
}

.Total > .TotalAmount{
    font-size: 1.7em;
    margin-left: 15px;
}

.Total > .TotalDescription > span {
    text-transform: uppercase;
    font-weight: 700;
}
/* .Record:nth-child(2n){
    background-color: rgb(240, 240, 240);
} */

.AppHeader {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    margin-top: 20px;
    margin-bottom: 20px;

    font-family: 'Lato', sans-serif;

}

.AppTitle {
    font-size: 3.0em;
    margin-bottom: 10px;
    font-weight: 300;
}
.MainTextAreaDiv{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.MainTextArea{
    display: block;
    width: 80vw;
    max-width: 500px;
    height: 300px;
    padding-top: 10px;
    margin-bottom: 20px;
    border-radius: 10px;
    resize: none;
}

.main-text-area-prompt{
    margin-left: 5px; 
    margin-bottom: 5px;
    font-family: 'Lato', sans-serif;
}

.SubmitButton{
    width: 100px;
    height: 30px;
    border-radius: 20px;
}

.name-entry {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 20px;
}

.name-entry textarea {
    resize: none;
    border-radius: 34px;
    margin-right: 19px;
    padding-top: 10px;
    padding-left: 22px;
    font-size: 1.1em;
    height: 30px;
    font-family: 'Lato', sans-serif;
    width: 100%;
}

.name-entry button {
    border-radius: 100%;
    width: 40px;
    height: 40px;
    background-color: #72cc50;
    color: white;
    font-size: 1.5em;
}

.name-entry button:hover{
    background-color: rgba(114, 205, 81, 0.672);
}

.name-entry-prompt {
    font-family: 'Lato', sans-serif;
    margin-bottom: 5px;
    margin-left: 5px;
}

.name-entry-prompt-ignore {
    font-size: 0.8em;
    color: rgba(0, 0, 0, 0.672);
}

.NameSelector{
    width: 63vw;
    max-width: 500px;
    margin-bottom: 20px ;
}

.name-selector-ignore .name-entry button {
    background-color: rgb(255, 130, 130); 
}
.new-name-list{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    grid-gap: 5px;
    gap: 5px; 
}

.new-person{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background-color: rgb(216, 216, 216);
    border-radius: 6px;
}

.new-person .new-person-name{
    text-align: center;
    vertical-align: middle;
    margin: 2px 6px;
    margin-bottom: 3px;
    margin-left: 10px;
}

.new-person button{
    background-color: unset;
    border: none;
    padding: 0px;
    padding-left: 3px;
    margin-right: 6px;
    cursor: pointer;
    border-left: 1px solid rgb(174, 174, 174);
}

.name-selector-ignore .new-person{
    background-color: rgb(255, 220, 220);
}


